async function fetchHandler(response) {
    if (!response.ok) {
      throw new HTTPResponseError(response);
    } else {
      // response.status >= 200 && response.status < 300
    }

  return response.json()
  .then(data => data)
  .catch(async error => {
    console.error(error);

    const errorBody = await error.response.text();
    console.error(`Error body: ${errorBody}`);
  })
}

exports.DataSources = {
  methods: {
    getTeam: async function () {
      return await fetch('http://localhost:3000/api/team', {
        method: 'get',
        headers: {'Content-Type': 'application/json'}
      })
      .then(fetchHandler)
    },
    
    getTeamStandup: async function (id, options={}) {
      const queryString = ( new URLSearchParams( options.query || {} ) ).toString()
      return await fetch(`http://localhost:3000/api/team/standup/${id}?${queryString}`, {
        method: 'get',
        headers: {'Content-Type': 'application/json'}
      })
      .then(fetchHandler)
    },
  }
}

