<template>
  <div class="team-standup-component" v-if="statusReport">
    <h1>{{ msg }}</h1>

    <div>{{ formattedDate }}</div>
    <StatusReport v-for="report in statusReport.reports" :key="report.id" :model="report"></StatusReport>

  </div>
</template>

<script>
import {DataSources} from '@/mixins'
import StatusReport from '@/components/StatusReport.vue'

export default {
  name: 'TeamStandup',
  components: {StatusReport},
  mixins: [DataSources],
  props: ['id', 'msg'],
  computed: {
    formattedDate: function() {
      return this.statusReport.date;
    },
  },
  data() {
    return {
      team: null,
      statusReport: null,
    }
  },
  methods: {
  },
  async mounted() {
    this.statusReport = await this.getTeamStandup(this.id, {query: this.$route.query})
  },
}
</script>

